<template>
<div @click="go" class="col-md-8 col-sm-12">
    <img src="../assets/splash.png" alt="Splash" />
</div>
</template>

<script>
import { useRouter } from 'vue2-helpers/vue-router'

export default {
  name: 'Splash',
  setup () {
    const router = useRouter()
    return {
      go: (event) => {
        console.log('Splash Image Clicked')
        router.push('login')
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
img {
    width:100%;
    height: 100%;
}
</style>



