<template>
<div id="container" @click="go" class="col-md-8 col-sm-12">
    <img  src="../assets/body_map_no_history.png" alt="bodymaphistory" />
</div>
</template>

<script>

import { useRouter } from 'vue2-helpers/vue-router'

export default {
  name: 'BodyMapNoHistory',
  setup () {
    const router = useRouter()
    return {
      go: (event) => {
        console.log('Body Map No History Image Clicked')
        router.push('camera')
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
img {
    width:100%;
    height: 100%;
}
</style>



