<template>
<div id="container" @click="go" class="col-md-8 col-sm-12">
    <img  src="../assets/qna.png" alt="qna" />
</div>
</template>

<script>

import { useRouter } from 'vue2-helpers/vue-router'
export default {
  name: 'QnA',
  setup () {
    const router = useRouter()
    return {
      go: (event) => {
        console.log('QnA Image Clicked')
        router.push('forgot-password')
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
img {
    width:100%;
    height: 100%;
}
</style>



