import Vue from 'vue'
import Router from 'vue-router'
import Splash from '@/components/Splash'
import Login from '@/components/Login'
import SignUp from '@/components/SignUp'
import Dashboard from '@/components/Dashboard'
import BodyMapHistory from '@/components/BodyMapHistory'
import BodyMapNoHistory from '@/components/BodyMapNoHistory'
import Camera from '@/components/Camera'
import SpotDetail from '@/components/SpotDetail'
import Result from '@/components/Result'
import SpotMap from '@/components/SpotMap'
import Gallery from '@/components/Gallery'
import GalleryDetail from '@/components/GalleryDetail'
import GalleryDetail2 from '@/components/GalleryDetail2'
import Settings from '@/components/Settings'
import Profile from '@/components/Profile'
import QnA from '@/components/QnA'
import ForgotPassword from '@/components/ForgotPassword'
import DiseaseInformation from '@/components/DiseaseInformation'
import DiseaseInformationDetail from '@/components/DiseaseInformationDetail'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Splash',
      component: Splash
    },
    {
      path: '/login',
      name: 'Login',
      component: Login
    },
    {
      path: '/signup',
      name: 'SignUp',
      component: SignUp
    },
    {
      path: '/dashboard',
      name: 'Dashboard',
      component: Dashboard
    },
    {
      path: '/body-map-history',
      name: 'Body Map History',
      component: BodyMapHistory
    },
    {
      path: '/body-map-no-history',
      name: 'Body Map No History',
      component: BodyMapNoHistory
    },
    {
      path: '/camera',
      name: 'Camera',
      component: Camera
    },
    {
      path: '/spot-detail',
      name: 'SpotDetail',
      component: SpotDetail
    },
    {
      path: '/result',
      name: 'Result',
      component: Result
    },
    {
      path: '/spot-map',
      name: 'SpotMap',
      component: SpotMap
    },
    {
      path: '/gallery',
      name: 'Gallery',
      component: Gallery
    },
    {
      path: '/gallery-detail',
      name: 'GalleryDetail',
      component: GalleryDetail
    },
    {
      path: '/gallery-detail2',
      name: 'GalleryDetail2',
      component: GalleryDetail2
    },
    {
      path: '/settings',
      name: 'Settings',
      component: Settings
    },
    {
      path: '/profile',
      name: 'Profile',
      component: Profile
    },
    {
      path: '/qna',
      name: 'QnA',
      component: QnA
    },
    {
      path: '/forgot-password',
      name: 'ForgotPassword',
      component: ForgotPassword
    },
    {
      path: '/disease-information',
      name: 'DiseaseInformation',
      component: DiseaseInformation
    },
    {
      path: '/disease-information-detail',
      name: 'DiseaseInformationDetail',
      component: DiseaseInformationDetail
    }
  ]
})



