<template>
<div id="container" @click="go" class="col-md-8 col-sm-12">
    <img  src="../assets/spot_detail.png" alt="spot_detail" />
</div>
</template>

<script>

import { useRouter } from 'vue2-helpers/vue-router'

export default {
  name: 'SpotDetail',
  setup () {
    const router = useRouter()
    return {
      go: (event) => {
        console.log('SpotDetail Image Clicked')
        router.push('result')
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
img {
    width:100%;
    height: 100%;
}
</style>



